.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    height: 77px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    height: 60px;
    align-items: flex-end;
    gap: 14px;
}

.imageAndText img {
    height: 48px;

}

.text {
    background-color: #0066CC;
    color: white;
    height: 60px;
    border-radius: 10px 10px 0px 10px;
    padding: 8px 13px;
    position: relative;
}

